.about {
  /* background: #23232b; */
  background: var(--dark-bg);
  /* color: #fff; */
  color: var(--light-bg);
  position: relative;
  padding: 2.5rem 1rem 2.5rem 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 270px;
  overflow: hidden;
}
.contentWrap {
  flex: 1;
  text-align: center;
  /* background-color: red; */
  margin: -100px;
  z-index: 1;
}

.contentWrap h2 {
  /* color: #FFD600; */
  color: var(--primary-yellow);
  font-family: 'Roasted Chicken', cursive;
  /* font-size: 1.5rem; */
  font-size: 4rem;
  margin-bottom: 1rem;
}

.contentWrap p {
  font-size: 1.5rem;
  margin: 0 auto;
  max-width: 700px;
  z-index: 20;
}

.verticalText {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  writing-mode: vertical-rl;
  text-orientation: mixed;
  font-size: 4rem;
  font-weight: 900;
  /* color: #60606e; */
  color: var(--light-bg);
  opacity: 0.1;
  letter-spacing: 2px;
  margin-right: 1rem;
  user-select: none;
  transition: 0.2s;
}
.verticalTextRight {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  writing-mode: vertical-rl;
  text-orientation: mixed;
  font-size: 4rem;
  font-weight: 900;
  color: var(--light-bg);
  opacity: 0.1;
  letter-spacing: 2px;
  margin-left: 1rem;
  user-select: none;
  transition: 0.2s
}
