/* Animation de flottement */
/* Animation de flottement */
.imageContainer {
  position: absolute;
  width: 100%;
  height: 100%;
}

.imageWrapper {
  position: absolute;
  width: 60px; /* Taille par défaut */
  height: 60px; /* Taille par défaut */
  transition: transform 0.3s ease-out; /* Ajoute une transition fluide */
}

.questionMark {
  width: 100%; /* L'image occupe tout le conteneur */
  height: 100%; /* L'image occupe tout le conteneur */
  animation: float 3s ease-in-out infinite; /* Applique l'animation de flottement */
}

/* Animation de flottement */
@keyframes float {
  0% {
    transform: translateY(0); /* Position initiale */
  }
  50% {
    transform: translateY(-10px); /* Monte légèrement */
  }
  100% {
    transform: translateY(0); /* Revient à la position initiale */
  }
}