.skills {
  /* background: #FFD600; */
  background: var(--primary-yellow);
  /* color: #23232b; */
  color: var(--dark-bg);
  padding: 2.5rem 0 2.5rem 0;
  text-align: center;
  /* height: 100vh; */
  height: 4500px;
  overflow: hidden;
  backdrop-filter: blur(0px);
}

.skills h2 {
  /* color: #FFD600; */
  font-family: 'Roasted Chicken', cursive;
  /* font-size: 1.5rem; */
  font-size: 4rem;
  margin-bottom: 1rem;
}
.skills p {
  max-width: 340px;
  margin: 0 auto;
  font-size: 1rem;
}

.skillsList {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  position: relative;
  padding-top: 100px;
  height: 100%; /* Permet à la section de s'étendre */
  margin: 0; /* Supprime les marges inutiles */
  backdrop-filter: blur(5px); /* Applique le flou à l'arrière-plan */
  align-items: flex-start; /* Aligne les items en haut */
}

.contentWrap {
  flex: 1;
  text-align: center;
  z-index: 1;
}

.skillItem {
  position: relative; /* Ensures bubbles are positioned relative to this */
  /* width: 50%; */
  min-width: 600px;
  padding: 1rem;
  border-top-left-radius: 40px;
  /* background-color: #23232b; */
  background-color: var(--dark-bg);
  /* color: #fff; */
  color: var(--light-bg);
  /* border-radius: 8px; */
  cursor: pointer;
  /* transform: translateY('100px'); */
  margin: 10px; /* Réduit l'espacement vertical */
  transition: 0.2s;
}

.skillItem:hover {
  /* transform: scale(1.05);  */
  background-color: white;
  color: var(--dark-bg);

}

.skillItem:hover .skillList {
  background-color: var(--dark-bg);
  color: var(--primary-yellow);
  font-family: 'consolas', monospace;
}

.skillItem.left {
  align-self: flex-start; /* Aligné à gauche */
  text-align: center;
  /* transform: translateX(100px); */
  margin-bottom: 100px;
}

.skillItem.right {
  align-self: flex-end; /* Aligné à droite */
  text-align: center;
  /* transform: translateX(-100px); */
  /* margin-top: -0px; */
  margin-bottom: 500px;
}

.skillTitle h3 {
  font-family: 'Impact', sans-serif;
  margin: 0;
  font-size: 4rem;
  text-transform: uppercase;
}

.skillList {
  margin-top: 1rem;
  list-style: none;
  padding: 0;
  /* display: none; Masque la sous-liste par défaut */
  /* background-color: red; */
  /* width: 100px;
  height: 100px; */
  position: relative;
  transition: 0.2s;
}

.skillItem:hover .skillList {
  /* display: block; */
}


.verticalText {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  writing-mode: vertical-rl;
  text-orientation: mixed;
  font-size: 4rem;
  font-weight: 900;
  color: var(--light-bg);
  opacity: 0.1;
  letter-spacing: 2px;
  margin-right: 1rem;
  user-select: none;
}
.verticalTextRight {
  position: aabsolute;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  writing-mode: vertical-rl;
  text-orientation: mixed;
  font-size: 4rem;
  font-weight: 900;
  color: var(--light-bg);
  opacity: 0.1;
  letter-spacing: 2px;
  margin-left: 1rem;
  user-select: none;
  
}


.imageContainer {
  position: absolute;
  width: 100%;
  height: 100%;
  /* background-color: red; */
  /* backdrop-filter: blur(30px); */
  overflow: hidden;
}

.imageWrapper {
  position: absolute;
  width: 50px;
  transition: 0.2s;
  /* height: 100px; */
  /* overflow: hidden; */
}

.questionMark {
  width: 100%;
  height: auto;
  animation: floatAnimation infinite;
}

.bubblesContainer {
  position: absolute; /* Allows bubbles to float outside the skillItem */
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 300px; /* Defines the area around the skillItem */
  height: 300px;
  pointer-events: none; /* Prevents interaction with bubbles */
  z-index: 1; /* Ensures bubbles are above the skillItem */
  /* background-color: red; */
}

.bubble {
  position: absolute; /* Each bubble is positioned independently */
  background-color: var(--primary-yellow);
  border: 5px solid var(--dark-bg);
  color: var(--dark-bg); /* Text color */
  border-radius: 50%; /* Makes the div circular */
  padding: 10px;
  font-size: 0.8rem;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  animation: wanderAnimation 10s infinite ease-in-out; /* Optional floating animation */
}

.skillListContainer{
  background-color: rgb(255, 255, 255);
  color: var(--dark-bg);
  font-family: 'Roasted Chicken', cursive;
  font-size: 2rem;
}

.hole{
  position: absolute;
  width: 50px;
  height: 50px;
  top: 7;
  right: 7;
  border-radius: 50%;
  background-color: var(--primary-yellow); /* Transparent to show the bubbles */
  z-index: -1; /* Behind the skillItem */
}



@keyframes floatAnimation {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px); /* Float up */
  }
  100% {
    transform: translateY(0); /* Return to original position */
  }
}

@keyframes wanderAnimation {
  0% {
    transform: translate(0, 0);
  }
  20% {
    transform: translate(10px, -5px);
  }
  40% {
    transform: translate(-8px, 12px);
  }
  60% {
    transform: translate(6px, -10px);
  }
  80% {
    transform: translate(-4px, 6px);
  }
  100% {
    transform: translate(0, 0);
  }
}

