.hero {
  /* background: #FFD600; */
  background-color: var(--primary-yellow);
  text-align: center;
  /* padding: 2rem 1rem 1.5rem 1rem; */
  position: relative;
  height: calc(100vh - 40px);
  overflow: hidden;
}

/* section, .hero{

} */

.imgWrap {
  width: 100vw;
  height: 100vh;
  margin: 0 auto; 
  overflow: hidden;
  /* border: 4px solid #23232b; */
  display: flex; /* Utilise Flexbox */
  justify-content: center; /* Centre horizontalement */
  align-items: flex-end; /* Aligne l'image sur le bas */
  position: relative; /* Nécessaire pour z-index */
  z-index: 1; /* Place devant textWrap2 */
  transition: 0.2s;
}

.profileImg {
  /* margin-top: 50px; */
  width: auto; /* Ajuste la largeur automatiquement */
  height: 100%; /* L'image occupe toute la hauteur */
  object-fit: cover; /* Maintient le ratio de l'image */
  object-position: bottom; /* Aligne l'image sur le bas */
}

.name {
  font-family: 'Impact', sans-serif;
  font-size: 25vw;
  letter-spacing: 7px;
  margin-bottom: -18px;
  /* color: #23232b; */
  color: var(--dark-bg);
  line-height: 1;
  opacity: 1;
  transition: 0.2s;
  animation: slideInFromBottom 1s ease-in-out; /* Applique l'animation */
}
.name2{
  position:absolute;
  top: 0;
  opacity: 0.30;
  z-index: -1;
  transition: 0.2s;
}
.surname {
  font-family: 'Impact', sans-serif;
  font-size: 4rem;
  /* color: #fff; */
  color: var(--light-bg);
  font-weight: 700;
  padding: 10px 20px 0 0; /* Remplace la marge par du padding pour inclure l'arrière-plan */
  background-color: var(--dark-bg); /* Affecte toute la zone */
  text-align: right;
  /* right: 10px; */
  user-select: none; /* Empêche la sélection du texte */
  animation: slideInFromRight 1s ease-in-out; /* Rend l'animation plus fluide */
  transition: 0.2s;
}

.textWrap {
  /* background-color: red; */
  position: absolute;
  bottom: 0px;
  width: 100%;
  /* padding-bottom: 40px; */
  /* opacity: 0.30; */
  /* margin-left: -10px; */
  /* padding-left: -50px; */
  z-index: 2; /* Place en premier plan */
}

.textWrap2 {
  position: absolute;
  bottom: 0px;
  width: 100%;
  /* padding-bottom: 40px; */

  z-index: 0; /* Place en arrière-plan */
}
.bandenoire {
  position: absolute;
  width: 100%;
  height: 79px; /* Hauteur de la bande noire */
  background-color: var(--dark-bg);
  /* background-color: red; */
  bottom: 0; /* Positionne la bande en bas */
  z-index: -1; /* Place derrière le texte */
}

@keyframes slideInFromRight {
  from {
    transform: translateY(100%); /* Commence hors de l'écran à droite */
    opacity: 0; /* Texte invisible au début */
  }
  to {
    transform: translateY(0); /* Arrive à sa position actuelle */
    opacity: 1; /* Texte complètement visible */
  }
}

@keyframes slideInFromBottom {
  from {
    transform: translateY(100%);
    /* opacity: 0; */
  }
  to {
    transform: translateY(0);
    /* opacity: 1; */
  }
}

@media (max-width: 425px) {
  .surname {
    /* padding: 5px 0; */
    font-size: 15vw; /* La taille de la police sera proportionnelle à la largeur de l'écran */
  }
}

@media (min-width: 900px) {
  .surname {
    /* padding: 40px 0; */
    font-size: 6vw; /* La taille de la police sera proportionnelle à la largeur de l'écran */
  }
}

@media (max-width: 600px) {
  .surname {
    text-align: center; /* Centre le texte horizontalement */
    margin: 0 auto; /* Supprime les marges et centre l'élément */
    padding: 10px; /* Ajuste le padding si nécessaire */
    font-size: 10vw; /* Ajuste la taille de la police pour les petits écrans */
  }
}