.navbar {
  background: #23232b;
  /* backdrop-filter: blur(30px); */
  /* opacity: 0.5; */
  padding: 0.5rem 0;
  position: sticky;
  top: 0;
  z-index: 100;
}
.navbar ul {
  display: flex;
  justify-content: flex-start;
  gap: 1rem;
  list-style: none;
  margin: 0;
  padding: 0 1rem;
}
.navbar li a {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  font-size: 0.95rem;
  transition: color 0.2s;
}
.navbar li a:hover {
  color: #FFD600;
}
