/* @import 'node_modules/locomotive-scroll/dist/locomotive-scroll'; */
html {
  scroll-behavior: smooth;
}

@font-face {
  font-family: 'Impact';
  src: url('../assets/impact.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'poppins medium';
  src: url('../assets/Poppins-Medium.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Roasted Chicken';
  src: url('../assets/Roasted Chicken.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}


body {
  margin: 0;
  font-family: 'poppins medium', Arial, sans-serif;
  background: #23232b;
  color: #23232b;
  scroll-behavior: smooth;
  user-select: none;
  overflow-x: hidden; /* Empêche le défilement horizontal */
  
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Montserrat', Arial, sans-serif;
  margin: 0;
}

* {
  box-sizing: border-box;
}

section {
  height: 100vh; /* 100% de la hauteur du viewport */
}