/* Animation de la vague */
@keyframes waveAnimation {
  0% {
    d: path("M0,30 Q125,80 250,30 T500,30 V80 H0 Z"); /* Position initiale */
  }
  50% {
    d: path("M0,40 Q125,60 250,40 T500,40 V80 H0 Z"); /* Modifie la forme de la vague */
  }
  100% {
    d: path("M0,30 Q125,80 250,30 T500,30 V80 H0 Z"); /* Retour à la position initiale */
  }
}

.wave {
  animation: waveAnimation 3s ease-in-out infinite; /* Animation indéfinie */
}