.education {
  position: relative;
  padding: 5rem 2rem;
  background-color: var(--light-bg);
  overflow: hidden;
}

.imageContainer {
  position: relative;
  width: 100%;
  height: auto;
}

.imageWrapper {
  position: absolute;
  width: 100px;
  height: 100px;
}

.educationIcon {
  width: 100%;
  height: auto;
  animation: floatAnimation 3s infinite ease-in-out;
}

@keyframes floatAnimation {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}

.verticalText,
.verticalTextRight {
  position: absolute;
  font-size: 2rem;
  color: #60606e;
}

.contentWrap {
  text-align: center;
}

.contentWrap h2 {
  font-size: 2.5rem;
  text-transform: uppercase;
}

.contentWrap p {
  font-size: 1rem;
  line-height: 1.5;
}