
.card2_main_fr {
    width: 430px;
    height: 250px;
    position: relative;
    background-color: rgba(244, 244, 244, 0.676);
    border-radius: 10px;
    cursor: pointer;
  }
  
  .card2_body_fr {
    width: var(--card-w);
    height: 100%;
    position: relative;
    overflow: hidden;
    border-radius: 20px;
  }
  
  .card2_im_fr {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: 0.4s;
  }
  
  .card2_info_fr {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    transition: 0.3s;
  }
  
  .card2_contim_fr {
    width: 100%;
    height: 100%;
    position: absolute;
    right: 0;
    transition: 0.3s;
  }
  
  .card2_im2_fr {
    height: 70%;
    float: none;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: 0.2s;
  }
  
  .card2_infofo_fr {
    background-color: rgba(47, 47, 47, 0.235);
    height: 100%;
    width: 0%;
    overflow: hidden;
    position: absolute;
    border-radius: 20px;
    left: 0;
    transition: 0.3s;
    padding: 0px;
  }
  
  .card2_prix_fr {
    background-color: white;
    height: 50px;
    border-radius: 20px;
    text-align: right;
    padding-right: 20px;
    font-size: 130%;
    padding-top: 5px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .card2_cat_fr {
    background-color: #FF2C2C;
    height: 100px;
    color: white;
    border-radius: 10px;
    text-align: left;
    padding-left: 20px;
    font-size: 80%;
    padding-top: 5px;
    margin-bottom: 10px;
  }
  
  .card2_qt_fr {
    background-color: white;
    height: 30px;
    border-radius: 20px;
    text-align: right;
    padding-right: 20px;
    font-size: 100%;
    padding-top: 2px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .card2_foot_fr {
    width: var(--card-w);
    height: 20%;
    font-weight: bold;
    text-align: center;
    padding: 10px;
    background-color: rgba(0, 0, 0, 0.461);
    position: absolute;
    color: rgb(255, 255, 255);
    backdrop-filter: blur(5px);
    bottom: 0;
    z-index: 1;
    border-radius: 20px;
    overflow: hidden;
  }
  
  /* Hover effects */
  .card2_main_fr:hover .card2_body_fr .card2_info_fr .card2_contim_fr {
    width: 40%;
  }
  
  .card2_main_fr:hover .card2_body_fr .card2_info_fr {
    backdrop-filter: blur(8px);
  }
  
  .card2_main_fr:hover .card2_body_fr .card2_im_fr {
    width: 120%;
    height: 120%;
  }
  
  .card2_main_fr:hover .card2_body_fr .card2_info_fr .card2_contim_fr .card2_im2_fr {
    height: 95%;
  }
  
  .card2_main_fr:hover .card2_body_fr .card2_info_fr .card2_infofo_fr {
    width: 60%;
    height: 100%;
    padding: 20px;
  }
  